.bill_container{
  display: -webkit-box;
  -webkit-box-orient:vertical;
  .child{
    display: -webkit-box;
    -webkit-box-pack:justify;
    min-height: 30px;
    padding: 10px 15px;
    border-bottom: 1px solid #e6e6e6;
    .left{
      display: -webkit-box;
      -webkit-box-orient:vertical;
      max-width: 70%;
      .name{
        font-size: 12px;
      }
      .date{
        font-size: 10px;
        color: #aaaaaa;
      }
    }
    .right{
      display: -webkit-box;
      -webkit-box-orient:vertical;
      text-align: end;
      max-width: 30%;
      .red{
        font-size: 12px;
        color: #ff7b78;
      }
      .green{
        font-size: 10px;
        color: #63cb76;
      }
      .gray{
        font-size: 10px;
        color: #aaaaaa;
      }
    }
  }
}